:root {
  --text-and-background-color-one: #f6f6f6;
  --text-and-background-color-two: #121212;
  --subtitel-and-background-color: #484848;
  --text-and-background-color-three: #173F35;
  --background-gradient-two: linear-gradient(
    0deg,
    rgba(18, 18, 18, 0.2),
    rgba(18, 18, 18, 0.2)
  );
  --background-gradient-one: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0) 70.73%,
    #121212 98.51%
  );
  --cubik-bezier: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
body {
  font-family:'Montserrat', sans-serif;;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.29;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.link {
  text-decoration: none;
}

.button{
  cursor: pointer;
}
.button:focus{
  background-color: inherit;
  border: 2px;
  border-color: #484848;
}

.container {
    width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}


.ul {
    padding: 0;
    margin: 0;
}

.no-scroll {
  overflow: hidden;
}

.flex {
  display: flex;
  align-items: center;
}

@media screen and (min-width: 768px) {
.container {
    padding-left: 20px;
  padding-right: 20px;
  }
  
}

@media screen and (min-width: 1440px) {
  .container {
    padding-left: 30px;
  padding-right: 30px;
  }
  
}